import { BasePage } from "../../pages/BasePage";
import { Forms } from "../../components/Forms";

export class TestPage extends BasePage {
	constructor(){
		super();
		this.form1data = {}
		this.form1errors ={}
		this.form1attributes = {};

		this.form1template = [
			{type:"text", name:"text1",title:"text1", validateRule:"required"},
			{type:"form", name:"form1",title:"form1", items:[
				{type:"text", name:"text2",title:"text2", validateRule:"required"},
			]},
			{type:"form", name:null, title:"form2", items:[
				{type:"text", name:"text3",title:"text3", validateRule:"required"},
			]},
		];

		this.form1 = new Forms(this.form1template, this.form1data, this.form1errors,  this.form1attributes,{nestedData:true});
	}
}


TestPage.template = `
	<div [component] = "this.form1"></div>
<pre>{{ JSON.stringify(this.form1template,null,'\t') }}</pre>
<pre>{{ JSON.stringify(this.form1data,null,'\t') }}</pre>
`;


